import { useState, useMemo, useContext } from 'react';
import { FiSearch } from 'react-icons/fi';
import {
  TextField,
  IconButton,
  ToggleButton,
  ToggleButtonGroup,
  FormControl,
  InputLabel,
} from '@mui/material'; // Brightness  import 
import {
  Brightness7Rounded, //   .
  Brightness4Rounded,
} from '@mui/icons-material';
import { ThemeProvider as MuiThemeProvider } from '@mui/material/styles';
import { lightTheme, darkTheme } from './theme';
import { ThemeContext } from './contexts/ThemeContext';
import ResultList from './components/ResultList';
import Pagination from './components/Pagination';
import Footer from './components/Footer';
import diseaseData from './data/disease.json';
import regionData from './data/region.json';
import medicationData from './data/medication.json';
import vaccinationData from './data/vaccination.json';
import etcData from './data/etc.json';

const categoryMap = {
  disease: '',
  region: '',
  medication: '',
  vaccination: '',
  region_travel: '',
  region_domestic_malaria: '',
  region_vcjd: '',
  region_malaria: '',
  etc: '',
};

const baseData = [
  ...diseaseData,
  ...medicationData,
  ...vaccinationData,
  ...etcData,
  ...regionData,
];

const processedData = baseData.map((item) => {
  const { restriction, category, countries } = item;
  let restrictionType = 'default';
  let restrictionPeriodDays = 0;
  let condition = '';
  let description = item.description || '';

  //    , description  .
  if (countries && countries.length > 0) {
    const countryNames = countries.map((c) => c.countryName).join(', ');
    description += ` ( : ${countryNames})`;
  }

  if (restriction) {
    restrictionType = restriction.type;
    condition = restriction.condition || '';

    switch (restriction.periodUnit) {
      case 'day':
        restrictionPeriodDays = restriction.periodValue;
        break;
      case 'week':
        restrictionPeriodDays = restriction.periodValue * 7;
        break;
      case 'month':
        restrictionPeriodDays = restriction.periodValue * 30; // Approximation
        break;
      case 'year':
        restrictionPeriodDays = restriction.periodValue * 365; // Approximation
        break;
      case 'permanent':
        restrictionPeriodDays = -1;
        break;
      default:
        restrictionPeriodDays = 0;
    }
  } else {
    restrictionType = 'none';
  }

  return {
    ...item,
    description,
    category: categoryMap[category] || '',
    restrictionType,
    restrictionPeriodDays,
    condition,
  };
});

//      
const malariaExceptionData = [];
regionData.forEach((region) => {
  if (region.id.includes('malaria') && region.countries) {
    region.countries.forEach((country) => {
      if (country.ruleType === 'exclusion') {
        country.areas.forEach((area) => {
          malariaExceptionData.push({
            id: `${region.id}-${country.countryName}-${area}`,
            category: '',
            name: `${country.countryName} - ${area}`,
            keywords: [country.countryName, area, ''],
            description: `    , ${area}    .`,
            allowable: true,
            isException: true,
            note: country.note,
            restriction: {},
            restrictionType: 'none',
            restrictionPeriodDays: 0,
            condition: '',
          });
        });
      }
    });
  }
});

const allData = [...processedData, ...malariaExceptionData];

function App() {
  const [query, setQuery] = useState('');
  const [filterType, setFilterType] = useState('');
  const [baseDate, setBaseDate] = useState(() =>
    new Date().toISOString().split('T')[0]
  );
  const [currentPage, setCurrentPage] = useState(1);
  const { theme, toggleTheme } = useContext(ThemeContext);
  const ITEMS_PER_PAGE = 20;

  const formatDate = (date) => {
    const yyyy = date.getFullYear();
    const mm = String(date.getMonth() + 1).padStart(2, '0');
    const dd = String(date.getDate()).padStart(2, '0');
    return `${yyyy}${mm}${dd}`;
  };

  const results = useMemo(() => {
    const lowerQuery = query.toLowerCase();

    if (lowerQuery) {
      //        
      return allData.reduce((acc, item) => {
        const lowerName = item.name.toLowerCase();

        if (lowerName.includes(lowerQuery)) {
          acc.push({ ...item, matchedKeyword: item.name });
        } else {
          const matchedKeyword = (item.keywords || []).find((k) =>
            k.toLowerCase().includes(lowerQuery)
          );
          if (matchedKeyword) {
            acc.push({ ...item, matchedKeyword });
          }
        }
        return acc;
      }, []);
    } else {
      //       
      return filterType
        ? allData.filter((item) => item.category === filterType)
        : allData;
    }
  }, [query, filterType]);

  const paginatedResults = useMemo(() => {
    const startIndex = (currentPage - 1) * ITEMS_PER_PAGE;
    const endIndex = startIndex + ITEMS_PER_PAGE;
    return results.slice(startIndex, endIndex);
  }, [results, currentPage]);

  return (
    <MuiThemeProvider theme={theme === 'light' ? lightTheme : darkTheme}>
      <div className="App p-4 sm:p-8 space-y-6 max-w-3xl mx-auto">
        {/*
            Grid    .
          1. 'grid' 'grid-cols-3'   3  .
          2. 'items-center'    .
        */}
        <header className="grid grid-cols-3 items-center">
          {/* 1() :  2     . */}
          <div></div>

          {/* 2() :   . */}
          <h1 className="text-lg sm:text-xl font-bold text-center">
              
          </h1>

          {/* 3() :    . */}
          <div className="flex justify-end">
            <IconButton
              onClick={toggleTheme}
              aria-label=" "
            >
              {theme === 'light' ? <Brightness4Rounded /> : <Brightness7Rounded />}
            </IconButton>
          </div>
        </header>

        {/*
                .
          1. 'flex-wrap'         .
          2. ('flex-grow')     ,      .
        */}
        <div className="flex flex-wrap justify-between items-center gap-3">
          <div className="flex items-center gap-3 flex-grow">
            <FiSearch />
            <TextField
              variant="outlined"
              placeholder=" "
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              size="small"
              className="flex-grow" // TextField    
            />
          </div>
          <TextField
            type="date"
            value={baseDate}
            onChange={(e) => setBaseDate(e.target.value)}
            size="small"
            inputProps={{ 'aria-label': ' ' }}
          />
        </div>

        {/*
            UI .
          1. 'flex-wrap'        .
          2. 'justify-center'     .
        */}
        <div className="flex justify-center">
          <FormControl
            size="small"
            aria-label=" "
            className={`flex-wrap ${
              query ? 'invisible' : ''
            } mt-3 sm:mt-0`}
          >
            <InputLabel shrink> </InputLabel>
            <ToggleButtonGroup
              exclusive
              size="small"
              value={filterType}
              onChange={(e, newType) => {
                if (newType !== null) {
                  setFilterType(newType);
                }
              }}
              className="flex-wrap" //     .
            >
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
            </ToggleButtonGroup>
          </FormControl>
        </div>

        <ResultList
          results={paginatedResults}
          query={query}
          baseDate={baseDate}
          formatDate={formatDate}
          totalResultsCount={results.length}
        />

        <div className="mt-8">
          <Pagination
            totalPages={Math.ceil(results.length / ITEMS_PER_PAGE)}
            currentPage={currentPage}
            onPageChange={setCurrentPage}
          />
        </div>

        <Footer />
      </div>
    </MuiThemeProvider>
  );
}

export default App;
